import type { AlbumListItem, ListShowType } from "@/views/album/albumList/types/albumList";
import { useStorage } from "@vueuse/core";
import { defineStore } from "pinia";

/**
 * @description: -store
 */
export const useAlbum = defineStore("album", () => {
  const albumList = useStorage<AlbumListItem[]>("albumList", [], localStorage); // 
  const albumListShowType = useStorage<ListShowType>("albumListShowType", "list", localStorage); // 

  return { albumList, albumListShowType };
});
